.projects {
	padding-top: 115px;

	.page-main__title {
		margin-bottom: 40px;
	}
}

.projects-description {
	width: 560px;
	margin: 0 auto;
	font-size: 22px;
	line-height: 33px;
	text-align: center;
}

.projects-list {
	display: flex;
	flex-wrap: wrap;
	margin: 0;
	padding-top: 60px;
	padding-bottom: 90px;
	list-style: none;
}

.projects-list__item {
	width: 560px;
}

.projects-help {
	display: flex;
	padding-top: 45px;
	padding-bottom: 45px;
	background-color: $light-grey;
}

.projects-help__mailto {
	margin-left: 350px;
	width: 560px;
	font-size: 22px;
	line-height: 34px;
}

.projects-list__item--info {
	margin: 0;
	font-size: 15px;
	line-height: 23px;
}

.projects-list__item--price {
	margin: 0;
	font-size: 20px;
	line-height: 27px;
	font-weight: 500;
	color: $blue;
}

.projects-list__item--heading {
	font-size: 25px;
	line-height: 34px;
	font-weight: 550;
	text-decoration: none;
	color: $blue;
}

.projects-list__item--text {
	padding-top: 8px;
	padding-bottom: 12px;

}

.projects-help >.page-main__wrapper {
	display: flex;
}